$font-stack: 'GothamPro', sans-serif;

@font-face {
  font-family: 'GothamPro';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro.ttf);
}

@font-face {
  font-family: 'GothamPro-Light';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Light.ttf);
}

@font-face {
  font-family: 'GothamPro-Black';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Black.ttf);
}

@font-face {
  font-family: 'GothamPro-Medium';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Medium.ttf);
}

$font : 'GothamPro';
$fontLight : 'GothamPro-Light';
$fontBlack : 'GothamPro-Black';
$fontMedium : 'GothamPro-Medium';


.none {
  padding: 0;
  margin: 0;
}

html {
  font-size: 18px;
}

body {
  font-family: $font-stack;
  font-size: 18px;
  color: #383838;
  width: 1349px !important;
}

.container {
  max-width: 1180px !important;
}

.menuBlock {
  width: 50%;
  min-height: 350px;
  height: 100%;
  max-height: 1100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9000;
  overflow: hidden;

  #menuHumburger {
    width: 25px;
    height: 25px;
    background-image: url(../img/sprite_x25.png);
    background-position: 0 0;
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 33px;
    right: 392px;
    z-index: 9999;
  }

  .backgroundCircle {
      background-color: #c8ae76;
      width: 25px;
      height: 25px;
      border-radius: 2500px;
      right: 57%;
      position: absolute;
      top: 33px;
      display: none;
  }
  ul {
    display: none;
    position: absolute;
    letter-spacing: 0.5px;
    z-index: 9999;
    right: 83px;
    top: 194px;
    font-size: 13.9px;
    list-style: none;
    color: #fff;
    margin: 0;
    padding: 0;
    width: 262px;
    li {
      margin-bottom: 10.5px;
      a {
        color: #fff;
        &:hover {
          color: #fff;
        }
      }
    }
    #separator {
      background-color: #e1e1e1;
      height: 1px;
      width: 100%;
      margin-top: 59px;
      margin-bottom: 24px;
    }
  }
}

header {
  position: relative;
  overflow: hidden;
  height: 769px;
  h1 {
    position: absolute;
    top: 29px;
    left: 58px;
    letter-spacing: 0.1px;
    font-size: 27.5px;
    font-family: GothamPro-Light;
  }

  h2 {
    position: absolute;
    top: 301px;
    left: 59px;
    letter-spacing: .3rem;
    font-size: 2.29rem;
    text-transform: uppercase;
    span {
      display: block;
      &:first-child {
        font-family: $fontLight;
      }
      &:last-child {
        font-family: $font;
        letter-spacing: 0.4rem;
      }
    }
  }
  img {
    position: absolute;
    top: 0;
    left: 120px;
    width: 555px;
    z-index: -9999;
  }

  .socialBlock {
    position: absolute;
    top: 31px;
    left: 392px;
    height: auto;
    z-index: 1;
    width: auto;
    a {
      img {
      width: 25px;
      height: auto;
      z-index: 0;
      position: relative;
      left: 0;
      }
    }
  }
  .arrowNext {
    position: absolute;
    z-index: 9999;
    bottom: 24px;
    margin-left: -9px;
    left: 0;
    height: auto;
    width: 19px;
    img {
      width: 100%;
      position: relative;
      z-index: 9999;
      left: 0;
    }
  }
  .row, .col-sm-6 {
    height: 100%;
  }

  .buttonAdditional {
    position: absolute;
    top: 456px;
    left: 60px;
  }
}

.screenOne, .screenThree {
  background-image: url(../img/W.png);
  background-repeat: no-repeat;
  background-position: 645px 215px;
  background-size: 521px;
  margin-bottom: 109px;
  overflow: hidden;
  img {
    width: 630px;
    margin-left: 111px;
    margin-top: 71px;
  }
  .titleScreen {
    text-transform: uppercase;
    color: #000;
    font-family: GothamPro;
    font-size: 17.5px;
    margin: 58px 0 0 242px;
    letter-spacing: 1.25px;
  }
  .bodyScreen {
    margin: 7px 0 0 242px;
    font-size: 10.8px;
    display: block;
    width: 405px;
    line-height: 17px;
    letter-spacing: 0.61px;
  }
  .separator {
    height: 1px;
    background-color: #b99e65;
    display: inline-block;
    width: 97px;
    margin: 0 0 2px 243px;
  }
  .buttonAdditional {
    width: 192px;
    text-align: center;
    margin: 39px 0 0 244px;
    font-size: 10px;
    padding: 7px 0 7px 0px;
    text-transform: unset;
    letter-spacing: 1.1px;
  }
}

.screenTwo, .screenFour {
  background-image: url(../img/П.png);
  background-repeat: no-repeat;
  background-position: 125px 173px;
  background-size: 284px;
  overflow: hidden;
  margin-bottom: 41px;
  img {
    width: 592px;
    margin-left: 364px;
    margin-top: 0;
  }
  .titleScreen {
    text-transform: uppercase;
    color: #000;
    font-family: GothamPro;
    font-size: 17px;
    margin: 57px 0 -2px 456px;
    letter-spacing: 1.6px;
  }
  .bodyScreen {
    margin: 9px 0 0 456px;
    font-size: 12px;
    display: block;
    width: 415px;
    line-height: 17px;
    letter-spacing: .61px;
  }
  .separator {
    height: 1px;
    background-color: #b99e65;
    display: inline-block;
    width: 97px;
    margin: 0 0 0 457px;
  }
  .buttonAdditional {
    width: 193px;
    text-align: center;
    margin: 39px 0 0 457px;
    font-size: 10px;
    padding: 7px 0 7px 2px;
    text-transform: unset;
    letter-spacing: 1px;
  }
}

.screenThree {
  background-image: url(../img/O.png);
  background-repeat: no-repeat;
  background-position: 670px 273px;
  background-size: 367px;
  margin-bottom: 110px;
}

.screenFour {
  background-image: url(../img/Т.png);
  background-repeat: no-repeat;
  background-position: 100px 175px;
  background-size: 283px;
  margin-bottom: 75px;
}

.button {
  letter-spacing: 1.6px;
  font-size: 10px;
  border: 1px solid #000;
  text-transform: uppercase;
  padding: 10px 50px 12px 58px;
  display: block;
  color: #000;
    &:hover, &:active {
      color: #000;
    }
}

.pagesIndex {
  width: 295px;
  position: fixed;
  right: 0;
  top: 35%;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 20px;
      margin-top: 21px;
      margin-left: 13px;
      &:first-child {
          margin-top: 0;
      }
      a {
        color: #7d7d7d;
        &:hover, &:active {
          color: #7d7d7d;
          text-decoration: none;
        }
        p {
          display: inline-block;
          font-size: 20px;
          margin: 0;
        }
        span {
          width: 233px;
          height: 2px;
          background-color: #444;
          display: none;
          vertical-align: top;
          margin-top: 22px;
          margin-left: 21px;
        }
      }
    }
    .active {
      margin-left: 0;
        a {
          color: #444;
          &:hover, &:active {
            color: #444;
            text-decoration: none;
          }
        }
        p {
          font-size: 27px;
        }
        span {
          display: inline-block;
        }
    }
  }
}


footer {
  padding: 40px 0 0 0;
  background-color: #f0f0f0;
  .container {
    padding: 0;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      font-size: 15px;
      letter-spacing: 1px;
      margin-bottom: 11px;
      a, a:hover {
        color: #000;
      }
    }
  }
  .col-sm-3 {
    &:nth-child(2) > ul {
      padding-left: 8px;
    }
    &:nth-child(3) {
      p {
        font-size: 16px;
        margin: -2px 0 44px 15px;
        letter-spacing: 0.3px;
        padding: 0;
        display: block;
        color: #000;
      }
      div {
        p {
          font-size: 16.4px;
          margin: 0 0 11px 14px;
          letter-spacing: 0.1px;
        }
        a {
          font-size: 16px;
          margin: -2px 0 0 15px;
          padding: 0;
          display: block;
          color: #000;
        }
      }
    }
    &:nth-child(4) {
      ul {
          margin-left: 22px;
          margin-top: 4px;
          margin-bottom: 56px;
          width: 275px;
        li {
          font-size: 14px;
          letter-spacing: 0;
          margin-bottom: 11px;
          display: inline-block;
          margin-right: 46px;
          &:last-child {
            margin-right: 0;
            margin-left: 8px;
          }
          a {
            display: inline-block;
            img {
              width: 28px;
            }
          }
          &:nth-child(2) > a > img {
              width: 34px;
          }
          &:nth-child(3) > a > img {
              width: 17px;
              margin-left: 3px;
          }
          &:last-child > a > img {
              width: 27px;
          }
        }
      }
      span {
        font-size: 16.39px;
        display: block;
        margin: -3px 0 0 22px;
        letter-spacing: -0.2px;
        width: 275px;
      }
    }
  }
  .bottomFooter {
    height: 69px;
    background-color: #383838;
    margin: 31px 0 0 0;
    .col-sm-6 > p {
      color: #f0f0f0;
      font-size: 13px;
      line-height: 16px;
      margin-top: 16px;
      letter-spacing: 0.65px;
    }
    .col-sm-3 {
      p {
        color: #f0f0f0;
        font-size: 13px;
        margin: 27px 0 0 17px;
        line-height: 16px;
        letter-spacing: 0.6px;
        width: 267px;
        a, a:hover {
          color: #f0f0f0;
          display: inline-block;
        }
      }
      &:last-child {
        p {
          margin-left: 24px;
        }
        a {
          color: #f0f0f0;
          font-size: 13px;
          display: inline-block;
          margin-left: 0;
        }
      }
    }
  }
}
